.RollDice{
    display: flex;
    flex-flow: column nowrap;
    min-height: 100vh;
}
.RollDice-container{
    display: flex;
    justify-content: center;
    align-content: center;
}
.RollDice button{
    align-self: center;
    width: 15em;
    padding: 1.5em;
    border: 0;
    border-radius: 10px;
    color: #fff;
    background-color: rgb(60, 68, 70);
    margin-top: 3em;
}

.RollDice button:hover{
    background-color: #266f72;
    cursor: pointer;
}

.RollDice button:disabled{
    background-color: hsla(182, 50%, 30%, 0.533);
    color: rgba(255, 255, 255, 0.555);
    font-style: italic;
}